* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
  overflow-x: hidden;
}

img {
  max-width: 100%;
  display: block;
}

nav {
  align-items: center;
  gap: 45px;
  padding: 20px 40px;
  display: flex;
}

.list-items {
  gap: 45px;
  display: flex;
}

.list-items li {
  margin: 10px 0;
  list-style: none;
}

.list-items li a, .go-back a {
  color: #000;
  font-weight: 500;
  text-decoration: none;
}

.box {
  width: 100%;
}

.box-jct {
  justify-content: center;
  margin-top: 80px;
  display: flex;
}

@media (max-width: 620px) {
  nav {
    flex-direction: column;
    gap: 45px;
    padding: 20px 40px;
    display: flex;
  }

  .list-items {
    flex-direction: column;
    gap: 45px;
    display: flex;
  }

  .box-jct {
    justify-content: center;
    margin-top: 0;
    display: flex;
  }
}

